@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Tangerine&display=swap);
html{
  scroll-behavior: smooth;

}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*WHOLE CONTAINER OF INFOSECTION AND FORM SECTION*/
.myContent {
  height: 100vh;
  width: 100%;
}

/*INFO SECTION TAKES 50% of the entire height*/
.infoSection {
  height: 700px;
  width: 100%;
}

/*FormSection Takes The 50% of the entire height*/
.formSection {
  height: auto;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Nunito";
}

/*PARENT is InfoSection, this takes 20% of the parent's height*/
.infoIntroduction {
  height: 15%;
  font-size: 3em;
  font-family: "Nunito";
}

/*PARENT is InfoSection, this takes 80% of the parent's height*/
.infoGrid {
    width: 100%;
  height: 80%;
  display: flex;
  flex-flow: row;
  justify-content: space-evenly;
  align-items: center;
}

/*Parent is infoGrid, this is a single Card*/
.myCard {
  background-color: white;
  height: 95%;
  min-height: 300px;
  width: 28%;
  display: flex;
  justify-content: center;
  font-family: "Nunito";
}

.cardContent {
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  width: 85%;
  margin-left: 20%;
  height: 100%;
  text-align: left;
}

.myCard .myImage {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.myCard .myInfo {
  font-size: 1.2em;
}

.myCard .myDetails a {
  background-color:#6e0d2c;
  color: white;
  text-decoration: none;
  font-weight: bold;
  padding: 0.8em;
}

.myCard .myQuestion {
  font-size: 1.8em;
  font-weight: bold;
  color: rgb(34, 31, 31);
}

/*FORM STYLING*/

.myForm {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  text-align: left;
  width: 92%;
  height: auto;
  background-color: white;
}


.formQuestion {
  width: 100%;
  height: 150px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  background-color: #6e0d2c;
  color: white;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.formMargin {
  margin-left: 10%;
}

.formQuestion span {
  font-size: 2.5em;
  font-weight: bold;
}
.formQuestion small {
  font-size: 1em;
  color: rgb(230, 230, 230);
}

.formRow {
  height: 150px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  width: 80%;
}

.formRow label {
  width: 100%;
  display: flex;
  justify-content: start;
  font-size: 1.3em;
  font-weight: bold;
  color: black;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: "Poppins";
  margin-bottom: 20px;
}

.formRow input {
  width: 100%;
  height: 50px;
}

.formRow input:-moz-ui-invalid{
  box-shadow: none;
}

.formRowButton {
  height: 150px;
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-flow: column;
}

.myTextBox {
  width: 80%;
  height: 100%;
  border: none;
  outline: none;
  background-color: #f6eef1;
  color: #6e0d2c;
  font-weight: bold;
  padding: 10px;
}

.myTextBox::-webkit-input-placeholder {
  color: #6e0d2c;
}

.myTextBox:-ms-input-placeholder {
  color: #6e0d2c;
}

.myTextBox::-ms-input-placeholder {
  color: #6e0d2c;
}

.myTextBox::placeholder {
  color: #6e0d2c;
}

.redPlaceHolder::-webkit-input-placeholder {
  color: red;
}

.redPlaceHolder:-ms-input-placeholder {
  color: red;
}

.redPlaceHolder::-ms-input-placeholder {
  color: red;
}

.redPlaceHolder::placeholder {
  color: red;
}

.goButton {
  background-color: #6e0d2c;
  border: none;
  padding: 1em;
  color: white;
  outline: none;
  font-weight: bold;
  font-size: 1.3em;
  margin-bottom: 30px;
}

.goButton:active {
  background-color: white;
  border: 0.5px solid black;
  color: #6e0d2c;
}

@media screen and (max-width: 992px) {
  /*INFO SECTION*/
  .myContent {
    height: auto;
  }

  .infoGrid {
    display: flex;
    flex-flow: column;
    height: auto;
    width: 100%;
    margin: 1em 0 1em 0;
  }

  .myCard {
    width: 100%;
    height: 400px;
    display: flex;
    background-color: rgb(250, 250, 250);
    margin: 10px 0 10px 0;
  }

  .myCard .myQuestion {
    font-size: 2.5em;
  }

  .myCard .cardContent {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
    align-items: center;
    margin-left: 0;
  }

  .myCard .myInfo {
    font-size: 1.4em;
  }

  .myForm{
    width: 100%;
  }

  .infoSection{
    height: auto;
  }

}


@media (max-height:1200px){
  .myContent {
    height: auto;
  }

  .myCard{
    height: 400px;
    margin: 5% 0 5% 0;
  }

}



.textContainer{
        color: white;
        font-family: "Tangerine";
        z-index: inherit;
        -webkit-user-select:none;
            -ms-user-select:none;
                user-select:none;
        text-align:left;
        position: relative;
        width: 100vw;
        height: 100%;
        display: flex;
        flex-flow: column;
        align-items: flex-start;
        justify-content:center;
        min-height: 800px;
}

.textContainer button{
    font-family: "Poppins";
    font-size: 35px;
    outline: none;
    border: none;
    padding: 20px;
    margin-top: 5%;
    margin-left: 5%;
    background-color: white;
    color: black;
}



.textContainer button:hover{
    color: #58192C;
    font-weight: bold;

}

.textWrapper{
    z-index: +1;
    position: absolute;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 800px;
}

.bigText{
    line-height: .8em;
    font-size:10em;
    margin-left: 5%;

}

.smallText{
    position: relative;
    font-size:5em;
    margin-left: 5%;
    font-weight: bold;

}



@media screen and (max-width:1380px){

    .textContainer{
        justify-content:center;
        align-items: center;
    }

    .bigText{
        line-height: .8em;
        font-size:14vw;
        margin-left: 5%;
    }
    
    .smallText{
        font-size:6vw;
        margin-left: 5%;
    }
    
}

@media (max-width:768px){
    .textContainer button {
        font-size: 5vw;
    }
}

@media screen and (max-width:500px){
    .bigText{
        line-height: .8em;
        font-size:15vw;
        margin-left: 5%;
    }
    
    .smallText{
        font-size:8vw;
        margin-left: 5%;
    }
}

@media (max-height: 800px ){
    .textWrapper{
        height: 800px;
    }    
    .textContainer{
        height: 800px;
    }

}
 .myPrivate{
    display: flex;
    flex-flow: column;
    height: 100vh;
}

.myPrivateContent{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #eee;
}

.myContentSection{
    width: 80%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    background-color: #fff;
}

.formLabel{
    align-self: flex-start;
}
.myFormContent{
    display: flex;
    flex-flow: column;
    justify-content: center;
    width: 80%;
}

.myFormContent input{
    border:none;
    border-bottom: 1px solid black;
    background-color: transparent;
    outline: none;
    color: black;
}

.myFormContent button{
    font-family: 'Poppins';
    background-color:white;
    color:black;
    margin-top:20px;
    border-radius: 6px;
    border:1px solid black;
    outline:none;
    font-weight: bold;
    
}

.myFormContent button:hover{
    background-color: black;
    color:white;
} 












.myHeader{
    background-color: #ccc;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}
.myHeader span{
    color:black;
    font-family: 'Poppins';
    font-size: 30px;
}

.myLink{
    background-color: black;
